.fill-path {
    fill: var(--text-main);
}

.illustrate {
    max-height: 30rem;
}

.floating_anima_bulb {
    animation: float_bulb 4s ease-in-out infinite;
}
@keyframes float_bulb {
    0% {
        transform: translate(215px, 102px);
    }
    50% {
        transform: translate(215px, 87px);
    }
    100% {
        transform: translate(215px, 102px);
    }
}

.floating_anima_window {
    animation: float_window 4s ease-in-out infinite;
}
@keyframes float_window {
    0% {
        transform: translate(380px, 182px);
    }
    50% {
        transform: translate(380px, 167px);
    }
    100% {
        transform: translate(380px, 182px);
    }
}

.floating_anima_pictures {
    transform: translate(60px, 28px);
    /* animation: float_pictures 6s ease-in-out infinite; */
}

@media (min-width: 320px) {
    .illustrate {
        max-height: 14rem;
    }
}

@media (min-width: 375px) {
    .illustrate {
        max-height: 16rem;
    }
}

